<template>
    <ContentField>
        <div>
            <div class="title" style="text-align: center; height: 40%;
                    font-size: 35px; font-style: italic;
                    font-weight: 250; letter-spacing: 1px;
                    background-color: rgb(68,68,68);
                    width: 60%; margin: auto; 
                    padding-top: 10px; padding-bottom: 10px;
                    border-radius: 5px;">
                ToDo
            </div>
            <img v-bind:src="img" alt="" @click="click_maid()"
                style="height: 32vh; max-width: 165px; float: left; margin-top: 10%;">
            <div class="task-body" style="width: 60%; text-align: center; 
                    margin: auto;">

                <!-- 拉取数据库所有待办 -->
                <div v-for="todo in todolist" :key="todo" style="margin-top: 20px;">
                    <div class="todo-box" style="text-align: left;">
                        {{ todo.content }}
                        <button @click="finish(todo.id)" type="button" class="btn btn-success" style="float: right;">
                            完成</button>
                    </div>
                </div>

                <input v-model="input" type="text" style="margin-top: 20px;" class="form-control" id="usercode">

                <button @click="add_todo()" type="submit" class="btn btn-primary" style="margin-top: 20px;">添加</button>
            </div>
        </div>

    </ContentField>
</template>

<script>
import $ from 'jquery';
import { ref } from 'vue';
import ContentField from '../components/ContentField.vue'

export default {
    components: {
        ContentField
    },
    method: {

    },
    beforeRouteEnter(to, from, next) {
        document.title = '今日待办'
        next()
    },
    setup() {
        let todolist = ref('');
        let input = ref('');
        const img1 = 'http://v6.hnitro.top/upload/img/wuyu.png';
        const img2 = 'http://v6.hnitro.top/upload/img/wuyu2.png';
        let img = ref(''); img.value = img1;
        console.log(img.value);

        function click_maid() {
            alert("早...早上好主人～")
        }

        function change_img() {
            if (img.value === img1) {
                img.value = img2;
            } else img.value = img1;

            alert("💗💗💗幸苦了，狗修金sama！💗💗💗")

        }


        function finish(id) {
            console.log("click button")
            $.ajax({
                url: "http://v6.hnitro.top/hhh/",
                type: "POST",
                data: JSON.stringify({ code: id }),
                headers: {
                    "Content-Type": "application/json"
                },
                success() {
                    $.ajax({
                        url: "http://v6.hnitro.top/todo/",
                        type: "GET",
                        success(resp) {
                            todolist.value = resp;
                            console.log("success")
                            change_img();
                        }
                    })
                }
            })
        }
        function add_todo() {
            console.log(input.value)
            $.ajax({
                url: "http://v6.hnitro.top/addtodo/",
                type: "POST",
                data: {
                    content: input.value,
                },
                success() {
                    $.ajax({
                        url: "http://v6.hnitro.top/todo/",
                        type: "GET",
                        data: {},
                        success(resp) {
                            todolist.value = resp;
                        }
                    })
                }
            })
        }
        $.ajax({
            url: "http://v6.hnitro.top/todo/",
            type: "GET",
            data: {},
            success(resp) {
                todolist.value = resp;
            }
        })
        return {
            todolist, add_todo, input, finish,
            change_img, img, click_maid,
        }
    },

}
</script>

<style scoped>
.todo-box {
    border-radius: 4px;
    height: 37px;
}

.todo-box:hover {
    background-color: green;
}
</style>