<template>
    <PostBar>
    </PostBar>
    <div class="text-center" style="display: flex;">
        <div class="leftside blog-box" style="font-size: 120%; color: #CBC6C0;">
            <div class="post-category">
                <h3 class="category-title" style="margin: 20px 0px 20px 0px;">分类专栏</h3>
                <div v-for="(count, tag) in blog_category" :key="tag" class="blog-categoryDetail"
                    @click="gotoAttachedTag(tag)">
                    <div class="left" style="float: left; margin-left: 10%;"> {{ tag }}</div>
                    <div class=" right" style="text-align: right; margin-right: 10%; align-content: center;">{{
                    count }}</div>
                    <hr>
                </div>
            </div>

        </div>
        <div class="rightside">

            <div class="container">
                <!-- 动态展示文章列表 -->
                <div v-for="article in articles" :key="article.id" class="blog-box" style="display: flex;">
                    <div class="blog-box-left" style="margin: auto; width: 35%;">
                        <img :src="article.pic_url" :srcset="article.pic_url" sizes="(max-width: 600px) 480px,
         800px" alt="blog-info-pic" class="blog-box-pic" @click="OpenArticle(article.id)">
                    </div>

                    <div class="blog-box-right-info">
                        <div class="blog-box-right-info-title" @click="OpenArticle(article.id)">{{ article.title }}
                        </div>
                        <div class="blog-box-right-info-date">
                            {{ article.createtime.substr(0, 10) + " " +
                    article.createtime.substr(11, 8) }}</div>

                        <!-- 文章标签 -->
                        <div class="tagdiv">
                            <span v-for="tag in article.tags" :key="tag" class="tag"> {{ tag }} </span>
                        </div>
                    </div>
                </div>
                <button v-if="id != 0" type="button" class="btn btn-secondary" @click="prePage()"
                    style="float: left; margin-top: 20px; margin-bottom: 200px;">上一页</button>
                <button v-if="id != len" type="button" class="btn btn-secondary" @click="nextPage()"
                    style="float: right; margin-top: 20px; margin-bottom: 200px;">下一页</button>
            </div>
        </div>

    </div>
</template>

<script>
import PostBar from '../../components/PostBar.vue'
import $ from 'jquery'
import { ref, watchEffect } from 'vue'
import router from '@/router';
import { useRoute } from 'vue-router';

export default {
    components: {
        PostBar,
    },
    beforeRouteEnter(to, from, next) {
        document.title = '博客';
        next();
    },
    setup() {
        const route = useRoute();
        let id = ref(parseInt(route.params.recordId));
        let len = ref('');
        if (isNaN(id.value)) id.value = 1;
        let articles = ref({});

        $.ajax({
            url: "http://v6.hnitro.top/post",
            type: "GET",
            data: {
            },
            success(resp) {
                articles.value = resp.results;
                console.log(articles.value)
            }
        });

        const refresh = (id) => {
            $.ajax({
                url: "http://v6.hnitro.top/post/?page=" + id,
                type: "GET",
                data: {
                },
                success(resp) {
                    console.log(resp)
                    articles.value = resp.results;
                }
            });
        }

        watchEffect(() => {
            console.log('id has changed to:', id.value);
            // 在这里可以执行想要重新执行的函数
            refresh(id.value);
        });

        const prePage = () => {
            if (id.value - 1 === 0) { return }
            id.value = id.value - 1;
            let recordId = id.value;
            router.push({
                name: "record_index_next",
                params: {
                    recordId
                },
            })
        }

        const nextPage = () => {
            id.value = id.value + 1;
            let recordId = id.value;

            router.push({
                name: "record_index_next",
                params: {
                    recordId
                },
            })
        }


        const OpenArticle = ArticleId => {

            router.push({
                name: "articleprofile",  // 这是按照路由的name匹配而不是路由的字符
                params: {
                    ArticleId, // 路由那里也是名字匹配 我服了
                },
            })
        }

        // 获取文章分类
        let blog_category = ref('');
        $.ajax({
            url: "http://v6.hnitro.top/getBlogCategory/",
            type: "GET",
            success(resp) {
                console.log(resp);
                blog_category.value = resp;
            }
        })

        const gotoAttachedTag = tag => {
            console.log(tag)
            router.push({
                name: "searchPost",
                params: { mytag: tag }
            })
        }

        return {
            articles,
            blog_category,
            OpenArticle,
            nextPage, id, refresh, prePage, len,
            gotoAttachedTag
        }
    },
}
</script>

