<template>
  <PasteBinCard>
    <div class="context">
      <h1 style="text-align: center;">{{ title }}</h1>
      <hr>
      <v-md-preview :text="markdown"></v-md-preview>
      <ContentField></ContentField>
    </div>
  </PasteBinCard>
</template>

<script>
import PasteBinCard from '../../components/PasteBinCard.vue'
import ContentField from '../../components/CommentField.vue'
import $ from 'jquery'
import { ref } from 'vue';
import { useRoute } from 'vue-router';

export default {
  components: {
    PasteBinCard,
    ContentField
  },
  beforeRouteEnter(to, from, next) {
    next();
  },
  setup() {
    let markdown = ref('');
    let title = ref('');
    const route = useRoute();
    const id = parseInt(route.params.ArticleId) // 从路由拿id ajax请求对应id 后端从request中拿id
    let turl = "http://v6.hnitro.top/post/" + id + '/';

    $.ajax({
      url: turl,
      type: "GET",
      data: {
        id,
      },
      success(resp) {
        console.log(resp);
        markdown.value = resp.content;
        title.value = resp.title;
        document.title = '[文章]  ' + title.value; // 既然是全局对象直接在这改标签题目 hhh
      }
    })

    return {
      markdown,
      title,
    }
  }
}
</script>

<style scoped></style>
